.wrapper {
  min-height: 100%;
  height: 100vh;
  display: grid;
  grid-template: minmax(100px, auto) 1fr minmax(70px, auto) / 1fr;
  grid-template-areas:
    "header"
    "main"
    "footer";
}
.header {
  grid-area: header;
  /* padding: 15px 0 0 0; */
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1280px) 1fr;
}
.ul_header {
  background-color: rgb(255, 255, 255);
  border-bottom: solid 1px rgb(148, 148, 148);
  display: grid;
  grid-auto-flow: column;
  justify-items: center;
  align-items: center;
  grid-column: 2/3;
}
.item_logo {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  background-color: azure;
}
.main_section {
  grid-area: main;
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1280px) 1fr;
}
.main_content {
  background-color: rgb(255, 255, 255);
  padding: 30px 15px;
  grid-column: 2/3;
}
.footer {
  grid-area: footer;
  display: grid;
  grid-template: 1fr / 1fr minmax(auto, 1280px) 1fr;
}

.footer_content {
  background-color: brown;
  padding: 15px;
  grid-column: 2/3;
}

.li_produkt {
  background-color: rgb(255, 255, 255);
  position: relative;
  padding: 10px;
  border: solid 1px rgb(148, 148, 148);
  cursor: pointer;
}
.img_produkt {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  /* height: 100%; */
  padding: 5% 0 15% 0;
  object-fit: cover;
}
.li_produkt::before {
  display: flex;
  content: attr(data-description);
  position: absolute;
  padding: 10px;
  top: 0;
  left: 0;
  width: calc(100% - 20px);
  height: 100%;
  background-color: #ffffffd4;
  align-items: center;
  transition: all 0.1s ease-out;
  visibility: hidden;
  z-index: -1;
  opacity: 0;
}
.li_produkt:hover:before {
  opacity: 1;
  visibility: visible;
  z-index: 1;
}
.ul_produkt {
  position: relative;
  display: grid;
  gap: 10px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
